export default {
	data() {
		return {
			n: true,
			pool: '',
			tabCurrentIndex: 0,
			navList: [
				{
					state: 0,
					text: '最新'
				},
				{
					state: 1,
					text: '排行'
				},
				{
					state: 2,
					text: '已购买'
				}
			],
			flag: true
		};
	},
	onLoad() {
		uni.request({
			url: this.url + 'cloud/',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.pool = res.data.data;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		},
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that = this;
			that.tabCurrentIndex = index;
		},
		buy: function(item) {
			uni.navigateTo({
				url: '../poolDetails/poolDetails?cloudid=' + item
			});
		}
	}
};
